 * 2. Disable pointer-events on svgs so buttons with icons handle clicks correctly.
 * https://css-tricks.com/slightly-careful-sub-elements-clickable-things/
*/
.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  pointer-events: none;
  /* 2 */
  vertical-align: middle;
  /* 1 */ }

