// adapted from Bootstrap v4.1.1
// https://github.com/twbs/bootstrap/blob/v4.1.1/scss/mixins/_grid-framework.scss

@mixin make-grid-columns($columns: $grid-columns, $breakpoints: $breakpoints) {
    // use placeholders and extend so selectors are combined when css is generated
    %grid-column {
      @include make-col-ready;
    }
  
    @each $breakpoint, $variant in $responsive-variants {
        // Allow columns to stretch full width below their breakpoints
        @for $i from 1 through $columns {
            .col#{$variant}-#{$i} {
                @extend %grid-column;
            }
        }

        .col#{$variant},
        .col#{$variant}-auto {
            @extend %grid-column;
        }

        @include breakpoint($breakpoint) {
            // Provide basic `.col-{bp}` classes for equal-width flexbox columns
            .col#{$variant} {
                flex-basis: 0;
                flex-grow: 1;
                max-width: 100%;
            }

            .col#{$variant}-auto {
                flex: 0 0 auto;
                width: auto;
                max-width: none; // Reset earlier grid tiers
            }

            @for $i from 1 through $columns {
                .col#{$variant}-#{$i} {
                    @include make-col($i, $columns);
                }
            }

            // `$columns - 1` because offsetting by the width of an entire row isn't possible
            @for $i from 0 through ($columns - 1) {
                // Avoid emitting useless .offset-0
                @if not($variant == '' and $i == 0) {
                    .offset#{$variant}-#{$i} {
                        @include make-col-offset($i, $columns);
                    }
                }
            }
        }
    }
}